@import url(https://fonts.googleapis.com/css2?family=Lato:wgh@300;400;700&display=swap);
::-webkit-scrollbar{
  width:5px;
  height: 6px;
}

::-webkit-scrollbar-track{
  box-shadow: inset 0 0 5px;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: #3ea175;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover{
  background: #a5aaad;
}

*{
  margin:0;
  padding:0;
}

body{
  box-sizing: border-box;
  font-family: "Late", sans-serif;
}

.text-primary-p{
  color: #a5aaad;
  font-size: 14px;
  font-weight: 700;
}

.font-bold{
  font-weight:700;
}

.text-title{
  color:#2e4a66;
}

.text-lightblue{
  color:#469cac;
}

.text-red{
  color:#cc3d38;
}

.text-yellow{
  color:#a98921;
}

.text-green{
  color:#3b9668;
}

.container{
  display: grid;
  height: 100vh;
  grid-template-columns: 0.8fr 1fr 1fr 1fr;
  grid-template-rows: 0.2fr 3fr;
  grid-template-areas:
    "sidebar nav nav nav"
    "sidebar main main main main";
}

@media only screen and (max-width: 978px){
  .container{
    grid-template-columns: 1fr;
    grid-template-rows: 0.2fr 3fr;
    grid-template-areas:
    "nav"
    "main";
  }
}
.header {
    height: 60px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    background-color: #151B54;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 100;
  }
  .items{
    display:-webkit-flex;
    display:flex;
    margin-left:10rem;
    -webkit-align-items: center;
            align-items: center;
    color:white;
  }
  
  .header__logo {
    width: 100px;
    object-fit: contain;
    margin-left:25px;
  }
  
  .header__search {
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
  } 
 
  .header__nav {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
  }
  
  .header__option {
    display: -webkit-flex;
