  }
.navbar {
    background-color: #151B54;
    height: 60px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: start;
            justify-content: start;
    -webkit-align-items: center;
            align-items: center;
    margin-left:2rem;
    height: auto;
    
  }
  
  .menu-bars {
    margin-left: 0rem;
    font-size: 1.5rem;
    background: none;
  }
  
  .nav-menu {
    background-color:#151B54;
    width: 250px;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
  }
  
  .nav-menu.active {
    left: 0;
    transition: 350ms;
  }
  
  .nav-text {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: start;
            justify-content: start;
    -webkit-align-items: center;
            align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
    color: white;
  }
  
  .nav-text a {
    text-decoration: none;
    color: white;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding: 0 16px;
    border-radius: 4px;
  }
  
  .nav-text a:hover {
    background-color: rgb(3, 184, 3);
  }
  
  .nav-menu-items {
    width: 100%;
  }
  
  .navbar-toggle {
    background-color:#151B54;
    width: 100%;
    height: 60px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
    margin-left:-12px;
    color:white;
  }
  
